import Vue from 'vue'
import App from './App.vue'
import './registerServiceWorker'
import router from './router'
import { library } from '@fortawesome/fontawesome-svg-core';
import { faCalendar, faEnvelope, faCheck, faUpload, faLock, faPhone, faClock } from '@fortawesome/free-solid-svg-icons';
import { faDiscord, faCodepen } from '@fortawesome/free-brands-svg-icons';
import { FontAwesomeIcon, FontAwesomeLayers, FontAwesomeLayersText } from '@fortawesome/vue-fontawesome';

library.add(faCalendar, faDiscord, faCodepen, faCheck, faEnvelope, faUpload, faLock, faPhone, faClock);

Vue.component('font-awesome-icon', FontAwesomeIcon)
Vue.component('font-awesome-layers', FontAwesomeLayers)
Vue.component('font-awesome-layers-text', FontAwesomeLayersText)

Vue.config.productionTip = false

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
