import Vue from 'vue';
import VueRouter from 'vue-router';

import Home from './views/Home.vue';
import Forms from './views/Forms.vue';
import FormInput from './views/FormInput.vue';
import FormSubmit from './views/FormSubmit.vue';
import DetailsStatic from './views/DetailsStatic.vue';
import Selenium from './views/Selenium.vue';
import SeleniumWait from './views/SeleniumWait.vue';
import SeleniumLocator from './views/SeleniumLocator.vue';
import SeleniumLocators from './views/SeleniumLocators.vue';
import SeleniumCSSSelectors from './views/SeleniumCSSSelectors.vue';
import About from './views/About.vue';
import NotFound from './views/NotFound.vue';

Vue.use(VueRouter);

export default new VueRouter({
    mode: 'history',
    base: "/scrapingapp/",
    scrollBehavior() {
        return { x: 0, y: 300 }
    },
    routes: [
        {
            path: '/',
            component: Home, 
            meta: {
                title: 'Home To Examples',
                metaTags: [
                    {
                        name: 'description',
                        content: 'This website includes different selenium example code on using different functionalities of selenium using python as the language. Selenium How To\'s.'
                    },
                    {
                        property: 'og:description',
                        content: 'This website includes different selenium example code on using different functionalities of selenium using python as the language. Selenium How To\'s'
                    }
                ]
            }
        },
        { path: '/forms', component: Forms },
        { path: '/formsubmit', component: FormSubmit },
        { path: '/forminput', component: FormInput },
        { path: '/detailsstatic', component: DetailsStatic },
        { path: '/selenium', component: Selenium },
        { path: '/seleniumwait', component: SeleniumWait },
        { path: '/seleniumlocator', component: SeleniumLocator },
        { path: '/seleniumlocators', component: SeleniumLocators },
        { path: '/seleniumselectors', component: SeleniumCSSSelectors },
        { path: '/about', component: About },
        { path: '/*', component: NotFound },
    ]
});

