<template>
    <div id="formElements">
        <div class="columns is-multiline divider-top" id="formInput">
            <div id="formButtons" class="column is-one-quarter divider-top" >
                <div class="field">
                    <label class="label">Buttons</label>
                    <div class="control">
                        <input class="button" type="button" value="Click Me" @click="handleButtonClick">
                        <input class="button" type="button" value="Im Disabled" disabled @click="handleButtonClick">
                    </div>
                </div>
            </div>
            <div id="formCheckboxes" class="column is-one-quarter divider-top">
                <div class="field">
                    <label class="label">Checkboxes</label>
                    <div class="control">
                        <input type="checkbox" value="checked" @click="handleCheckedClick"> Enabled <br/>
                        <input type="checkbox" value="disabledChecked" disabled @click="handleCheckedClick"> Disabled
                    </div>
                </div>
            </div>
            <div id="formColor" class="column is-one-quarter divider-top">
                <div class="field">
                    <label class="label">Color</label>
                    <div class="control">
                        <input type="color">
                    </div>
                </div>
            </div>
            <div id="formDate" class="column is-one-quarter divider-top">
                <div class="field">
                    <label class="label">Date</label>
                    <div class="control">
                        <form @submit="handleSubmit($event)">
                            <p class="control has-icons-left has-icons-right">
                                <input class="input" type="date" name="trip-start" value="2020-07-22" min="2020-01-01" max="2020-12-31">
                                <span class="icon is-small is-left">
                                    <font-awesome-icon icon="calendar" />
                                </span>
                                <span class="icon is-small is-right">
                                    <font-awesome-icon icon="check" />
                                </span>
                            </p>
                            <input class="button"  type="submit" value="Submit">
                        </form>
                    </div>
                </div>
            </div>
            <div id="formEmail" class="column is-one-quarter divider-top">
                <div class="field">
                    <label class="label">Email (xxx@globex.com)</label>
                    <div class="control">
                        <form @submit="handleSubmit($event)">
                            <p class="control has-icons-left has-icons-right">
                                <input class="input" type="email" pattern=".+@seleniumbyexamples.com" size="30" placeholder="Email" required >
                                <span class="icon is-small is-left">
                                    <font-awesome-icon icon="envelope" />
                                </span>
                                <span class="icon is-small is-right">
                                    <font-awesome-icon icon="check" />
                                </span>
                            </p>
                            <input class="button"  type="submit" value="Submit">
                        </form>
                    </div>
                </div>
            </div>
            <div id="formFile" class="column is-one-quarter divider-top">
                <div class="field">
                    <label class="label">File Upload</label>
                    <div class="file has-name">
                        <label class="file-label">
                        <input class="file-input" type="file" ref="file" name="resume" @change="handleFileUpload">
                        <span class="file-cta">
                            <span class="file-icon">
                            <font-awesome-icon icon="upload" />
                            </span>
                        </span>
                        <span class="file-name">
                            {{ fileName }}
                        </span>
                        </label>
                    </div>
                </div>
            </div>
            <div id="formHidden" class="column is-one-quarter divider-top">
                <div class="field">
                    <label class="label">You can't see because its hidden</label>
                    <div class="control">
                        <input class="input" type="hidden" value="Im Hidden">
                    </div>
                </div>
            </div>
            <div id="formImage" class="column is-one-quarter divider-top">
                <div class="field">
                    <label class="label">Image</label>
                    <div class="control">
                        <input type="image" id="image" alt="HTML5" src="/scrapingapp/img/icons/HTML5_Logo_64.png">
                    </div>
                </div>
            </div>
            <div id="formNumber" class="column is-one-quarter divider-top">
                <div class="field">
                    <label class="label">Numeric</label>
                    <div class="control">
                        <input class="input" type="number">
                    </div>
                </div>
            </div>
            <div id="formPassword" class="column is-one-quarter divider-top">
                <div class="field">
                    <label class="label">Password</label>
                    <div class="control">
                        <p class="control has-icons-left has-icons-right">
                            <input class="input" type="password">
                            <span class="icon is-small is-left">
                                <font-awesome-icon icon="lock" />
                            </span>
                            <span class="icon is-small is-right">
                                <font-awesome-icon icon="check" />
                            </span>
                        </p>
                    </div>
                </div>
            </div>
            <div id="formRadio" class="column is-one-quarter divider-top">
                <div class="field">
                    <label class="label">Radio</label>
                    <div class="control">
                        <label class="radio">
                            <input type="radio" name="answer">
                            Yes
                        </label>
                        <label class="radio">
                            <input type="radio" name="answer">
                            No
                        </label>
                    </div>
                </div>
            </div>
            <div id="formRange" class="column is-one-quarter divider-top">
                <div class="field">
                    <label class="label">Range</label>
                    <div class="control">
                        <input type="range" id="cowbell" name="cowbell" min="0" max="100" value="90" step="10">
                    </div>
                </div>
            </div>
            <div id="formReset" class="column is-one-quarter divider-top">
                <div class="field">
                    <label class="label">Reset</label>
                    <div class="control">
                        <form>
                        <input class="input" type="text" value="This is for reset">
                        <input class="button"  type="reset" value="Reset">
                        </form>
                    </div>
                </div>
            </div>
            <div id="formSubmit" class="column is-one-quarter divider-top">
                <div class="field">
                    <label class="label">Submit</label>
                    <div class="control">
                        <form  @submit="handleSubmit($event)">
                            <input class="input" type="text" value="This is for submit">
                            <input class="button"  type="submit" value="Submit">
                        </form>
                    </div>
                </div>
            </div>
            <div id="formTelephone" class="column is-one-quarter divider-top">
                <div class="field">
                    <label class="label">Telephone (xxx-xxx-xxxx)</label>
                    <form @submit="handleSubmit($event)">
                    <p class="control has-icons-left has-icons-right">
                            <input class="input" type="tel" name="phone" pattern="[0-9]{3}-[0-9]{3}-[0-9]{4}" required>
                            <span class="icon is-small is-left">
                                <font-awesome-icon icon="phone" />
                            </span>
                            <span class="icon is-small is-right">
                                <font-awesome-icon icon="check" />
                            </span>
                            <input class="button"  type="submit" value="Submit">
                    </p>
                    </form>
                </div>
            </div>
            <div id="formText" class="column is-one-quarter divider-top">
                <div class="field">
                    <label class="label">Text</label>
                    <div class="control">
                        <input class="input" type="text" id="name" name="name" required>
                    </div>
                </div>
            </div>

            <div id="formTime" class="column is-one-quarter divider-top">
                <div class="field">
                    <label class="label">Time</label>
                    <form @submit="handleSubmit($event)">
                        <p class="control has-icons-left has-icons-right">
                            <input class="input"  type="time" id="appt" name="appt" min="09:00" max="18:00" required>
                            <span class="icon is-small is-left">
                                <font-awesome-icon icon="clock" />
                            </span>
                            <input class="button"  type="submit" value="Submit">
                        </p>
                    </form>
                </div>
            </div>
        </div>
        <div class="columns is-multiline divider-top" id="formTags">
            <div id="formElButtons" class="column is-one-quarter divider-top" >
                <div class="field">
                    <label class="label">Button Elements</label>
                    <div class="control">
                        <button class="button" type="button" @click="handleButtonClick">Click Me</button>
                        <button class="button" type="button" disabled @click="handleButtonClick">Im Disabled</button>
                    </div>
                </div>
            </div>
            <div id="formElDatalist" class="column is-one-quarter divider-top" >
                <div class="field">
                    <label class="label">Datalist - Desserts</label>
                    <div class="control">
                        <input class="input" list="ice-cream-flavors" id="ice-cream-choice" name="ice-cream-choice" />
                        <datalist id="ice-cream-flavors">
                            <option value="Cake"/>
                            <option value="Candies"/>
                            <option value="Chocolate"/>
                            <option value="Custards"/>
                            <option value="Ice Cream"/>
                            <option value="Jelly"/>
                            <option value="Pastries"/>
                            <option value="Pies"/>
                            <option value="Pudding"/>
                        </datalist>
                    </div>
                </div>
            </div>
            <div id="formElMeter" class="column is-one-quarter divider-top">
                <div class="field">
                    <label class="label">Meter</label>
                    <div class="control">
                        <meter min="0" max="100" low="10" high="90" optimum="75" value="30">at 30/100</meter>
                    </div>
                </div>
            </div>
            <div id="formElProgress" class="column is-one-quarter divider-top">
                <div class="field">
                    <label class="label">Progress Bar</label>
                    <div class="control">
                        <progress min="0" max="100" value="30">at 30%</progress>
                    </div>
                </div>
            </div>
            <div id="formElSelect" class="column is-one-quarter divider-top">
                <div class="field">
                    <label class="label">Select - Pets</label>
                    <div class="control">
                        <div  class="select">
                            <select>
                                <option value="dog">Dog</option>
                                <option value="cat">Cat</option>
                                <option value="hamster">Hamster</option>
                                <option value="parrot">Parrot</option>
                                <option value="goldfish">Goldfish</option>
                            </select>
                        </div>
                    </div>
                </div>
            </div>
            <div id="formElSelectMulti" class="column is-one-quarter divider-top">
                <div class="field">
                    <label class="label">Select - Pets</label>
                    <div class="control">
                        <div  class="select">
                            <select multiple>
                                <option value="dog">Dog</option>
                                <option value="cat">Cat</option>
                                <option value="hamster">Hamster</option>
                                <option value="parrot">Parrot</option>
                                <option value="goldfish">Goldfish</option>
                            </select>
                        </div>
                    </div>
                </div>
            </div>
            <div id="formElTextarea" class="column is-one-quarter divider-top">
                <div class="field">
                    <label class="label">Text Area</label>
                    <div class="control">
                        <textarea class="textarea" placeholder="Selenium By Examples"></textarea>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  name: "FormSubmit",
  data(){
      return {
        fileName: '',
        publicPath: process.env.BASE_URL
      }
  },
  methods: {
    handleButtonClick(){
        alert('Im clicked');
    },
    handleCheckedClick(){
        alert('Im checked');
    },
    handleFileUpload(){
        this.fileName = this.$refs.file.files[0].name;
    },
    handleSubmit(event){
        event.preventDefault();
        event.target.reset();
    }
  }
};
</script>
